* {
  margin: 0;
  padding: 0; }

html,
body {
  height: 100%; }

body {
  background: #423c51;
  font-family: arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: white; }

ul,
ol {
  list-style: none; }

li {
  display: inline-block; }

.root {
  max-width: 600px;
  margin: 0 auto; }

.rx_btn_icon,
.rx_btn_text,
.input_text,
.input_num {
  display: inline-block;
  line-height: 25px;
  vertical-align: middle;
  color: #423c51;
  background: white;
  border: 1px solid #555;
  outline: 0 none;
  margin-right: 5px; }

.rx_btn_icon,
.rx_btn_text {
  cursor: pointer; }

.rx_btn_icon,
.rx_btn_text,
.input_num {
  text-align: center; }

.rx_btn_text {
  background: red; }

.rx_btn_icon {
  width: 32px;
  font-weight: bold; }
  .rx_btn_icon.rx_checked {
    background: #a7a0b9; }
  .rx_btn_icon .fa-minus-square {
    font-size: 14px; }
  .rx_btn_icon .fa-level-down {
    font-size: 19px; }
  .rx_btn_icon .fa-level-up {
    font-size: 19px; }
  .rx_btn_icon .fa-text-height {
    font-size: 17px; }
  .rx_btn_icon .fa-exclamation {
    font-size: 18px; }
  .rx_btn_icon > input[type=radio],
  .rx_btn_icon > input[type=checkbox] {
    display: none; }

.rx-btn-edit {
  display: inline-block;
  font-size: 16px;
  color: #c2bece;
  cursor: pointer;
  padding: 0 0 0 10px; }
  .rx-btn-edit:hover, .rx-btn-edit.active {
    color: white; }

.input_text {
  width: 150px;
  padding: 0 5px; }

.input_num {
  width: 50px; }

.cate_val_wrapper {
  display: inline-block;
  padding: 5px 10px;
  margin-top: 10px;
  background: rgba(255, 255, 255, 0.3); }
  .cate_val_wrapper + a {
    display: inline-block;
    font-size: 21px;
    width: 32px;
    text-align: center;
    padding: 0 5px;
    vertical-align: middle;
    cursor: pointer; }
    .cate_val_wrapper + a .fa {
      -o-transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
      transform: rotate(-45deg); }

.input_tab > input {
  width: 100%;
  padding: 100px 10px; }



